body {
    font-family: 'Lato', sans-serif;
}

.main__container {
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/moviedb/static/media/background.364d7a13.jpg);
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/moviedb/static/media/background.364d7a13.jpg);
    background-size: cover;
    background-position: center;
    height: 100vh;
}

.main__container h1 {
    color: #fff;
    

}

.main__container__input {
    border: none;
    
    background: transparent;
    width: 200px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.3);
    -webkit-transition: width 0.5s;
    transition: width 0.5s;
    font-size: 25px;
    color: rgba(255, 255, 255, 0.6);
}

.main__container__input:focus {
    width: 300px;
    border: 2px solid rgb(141, 10, 10);
    outline: none;
    border-radius: 10px;
}

.main__container__header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 80%;
}



.autocomplete {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 10px;
    width: 300px;
    color: #000;
}

.autocomplete img {
    width: 20px;
    height: auto
}
.navbar {
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: transparent;
    height: 50px;
    width: 100%;
}

.navbar__logo {
    color: #fff;
}


.movie__container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.1), rgb(0, 0, 0)), url('https://www.indiewire.com/wp-content/uploads/2014/10/interstellar-poster.jpg?w=780');
    background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgb(0, 0, 0)), url('https://www.indiewire.com/wp-content/uploads/2014/10/interstellar-poster.jpg?w=780');
    font-size: 17px;
}

.movie__container p {
    color: #fff;
}

.movie__container h2 {
    color: #fff;
}

.movie__container h4 {
    color: #ffde03;
}

.movie__container span {
    color: rgb(255, 255, 255);
}

.movie__element {
    display: -webkit-flex;
    display: flex;
}

.movie__element img{
    height: auto;
    width: 500px;
}

.movie__element__content {
    height: auto;
    width: 500px;
    border: 1px solid rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.705);
    padding: 10px 15px 10px 15px;
}

.movie__info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.movie__info__properties {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.movie__loader {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    

}





@media (max-width: 60rem) {
    .movie__container {
        font-size: 0.7rem;
    }

    .movie__container h2 {
        font-size: 1rem;
    }
    
    .movie__container h4 {
        font-size: 1rem;
    }

    .movie__element img {
        height: auto;
        width: 17rem;
    }
    .movie__element__content {
        height: auto;
        width: 17rem;
        border: 1px solid #000;
        background-color: rgba(0, 0, 0, 0.705);
        padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    }
}

@media (max-width: 40rem) {
    .movie__container {
        font-size: 3rem;
    }

    .movie__container h2 {
        font-size: 1rem;
    }
    
    .movie__container h4 {
        font-size: 1rem;
    }

    .movie__element {
        display:-webkit-flex;
        display:flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .movie__element img {
        height: auto;
        width: 13rem;
    }
    .movie__element__content {
        height: auto;
        width: 13rem;
        padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    }
}

@media (max-width: 23rem) {
    .movie__container {
        font-size: 0.75rem;
    }
    .movie__element {
        display:-webkit-flex;
        display:flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .movie__element img {
        display: none;
    }
    .movie__element__content {
        height: auto;
        width: 15rem;
        padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    }
}

@media only screen 
and (min-device-width : 375px) 
and (max-device-width : 667px) 
{ 
    .movie__container {
        font-size: 0.75rem;
    }
    .movie__element {
        display:-webkit-flex;
        display:flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .movie__element img {
        display: none
    }
    .movie__element__content {
        height: auto;
        width: 15rem;
        padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    }
}
